.search-form {
  --main-focus: #2d8cf0;
  --font-color: #323232;
  --font-color-sub: #666;
  --bg-color: #fff;
  --main-color: #323232;
  width: 250px;
  height: 35px;
  box-shadow: 4px 4px var(--main-color);
  border-radius: 5px;
  flex-direction: row;
  margin: 0 auto;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  display: flex;
}

.search-input {
  width: 100%;
  height: 25px;
  border: 2px solid var(--main-color);
  color: var(--font-color);
  background: var(--bg-color);
  border-right: 0;
  border-radius: 5px 0 0 5px;
  padding: 5px 10px;
  font-size: 15px;
  font-weight: 500;
}

.search-btn {
  width: 100px;
  height: 35px;
  border: 2px solid var(--main-focus);
  background-color: var(--main-focus);
  letter-spacing: 1px;
  color: var(--bg-color);
  cursor: pointer;
  border-radius: 0 5px 5px 0;
  font-size: 15px;
  font-weight: 500;
}

.search-input:focus {
  border: 2px solid var(--main-focus);
  border-right: 0;
  outline: none;
}

.learn-more {
  cursor: pointer;
  vertical-align: middle;
  font-size: inherit;
  background: none;
  border: 0;
  outline: none;
  padding: 0;
  font-family: inherit;
  text-decoration: none;
  display: inline-block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

button.learn-more {
  width: 12rem;
  height: auto;
}

button.learn-more .circle {
  width: 3rem;
  height: 3rem;
  background: #282936;
  border-radius: 1.625rem;
  margin: 0;
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  display: block;
  position: relative;
}

button.learn-more .circle .icon {
  background: #fff;
  margin: auto;
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
}

button.learn-more .circle .icon.arrow {
  width: 1.125rem;
  height: .125rem;
  background: none;
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  left: .625rem;
}

button.learn-more .circle .icon.arrow:before {
  content: "";
  width: .625rem;
  height: .625rem;
  border-top: .125rem solid #fff;
  border-right: .125rem solid #fff;
  position: absolute;
  top: -.29rem;
  right: .0625rem;
  transform: rotate(45deg);
}

button.learn-more .button-text {
  color: #282936;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 0 1.85rem;
  padding: .75rem 0;
  font-weight: 700;
  line-height: 1.6;
  transition: all .45s cubic-bezier(.65, 0, .076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

button:hover .circle {
  width: 100%;
}

button:hover .circle .icon.arrow {
  background: #fff;
  transform: translate(1rem);
}

button:hover .button-text {
  color: #fff;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

img {
  width: 100%;
  height: 200px;
}

.photo-card {
  width: calc(25% - 15px);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

